import url from "../../../bin/url";
export default {
    data() {
        return {
            videoList: '',
            list:[1,2,3]
        }
    },
    methods: {
        // 获取列表
    getVideoList() {
      let _obj = {
        openId: localStorage.getItem("openId"),
        id: this.videoId
      };
      this.$fetch.post(url.getVideoList, _obj).then(
        data => {
          if (data.code == 0) {
            this.videoList = data
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    }
    },
    mounted() {
          this.getVideoList();
    },
    created() {
    this.routeParams = JSON.parse(this.$route.query.obj);
    console.log("567890-");
    this.videoId = this.routeParams.data.id;
  }
}



